export const commentCreate = (comment, user) => {
  return axios({
    url: apiUrl + '/comments',
    method: 'POST',
    headers: {
      'Authorization': `Bearer ${user.token}`
    },
    data: { comment }
  })
}

export const commentDelete = (threadId, id, user) => {
  return axios({
    url: apiUrl + '/comments/' + id,
    method: 'DELETE',
    headers: {
      'Authorization': `Bearer ${user.token}`
    },
    data: {
      comment: {
        threadId: threadId
      }
    }
  })
}

export const commentUpdate = (id, user, comment) => {
  return axios({
    url: apiUrl + '/comments/' + id,
    method: 'PATCH',
    headers: {
      'Authorization': `Bearer ${user.token}`
    },
    data: { comment }
  })
}



