import React, { Component } from 'react'
import UpdateCommentForm from '../Forms/UpdateCommentForm'
import { Redirect, Link, withRouter } from 'react-router-dom'
import { commentUpdate } from '../../api/commentsApi'

class CommentUpdate extends Component {
  constructor (props) {
    super(props)

    this.state = {
      comment: {
        body: '',
        name: '',
        id: '',
        threadId: ''
      },
      updated: false
    }
  }

  handleSubmit = (event) => {
    event.preventDefault()
    const { user, match, msgAlert } = this.props
    const { comment } = this.state
    commentUpdate(match.params.id, user, comment)
      .then(res => this.setState({ updated: true }))
      .catch(error => {
        msgAlert({
          heading: 'Updating Comment Failed',
          message: 'Comment was not updated due to error: ' + error.message,
          variant: 'danger'
        })
      })
  }

  handleChange = event => {
    event.persist()
    this.setState((state) => {
      return {
        comment: { ...state.comment, [event.target.name]: event.target.value }
      }
    })
  }

  render () {
    const { comment, updated } = this.state
    const { match, user } = this.props
    if (updated === true) {
      return <Redirect to={`/category/${match.params.category}/threads/${match.params.threadId}`} />
    }
    return (
      <div>
        <h3>Update Comment</h3>
        <UpdateCommentForm
          comment={comment}
          match={match}
          user={user}
          handleChange={this.handleChange}
          handleSubmit={this.handleSubmit}
        />
        <Link to={`/category/${match.params.category}/threads/${match.params.threadId}`}>Go back</Link>
      </div>
    )
  }
}

export default withRouter(CommentUpdate)



