import React, { Component } from 'react'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import Spinner from 'react-bootstrap/Spinner'
import { withRouter, Redirect, Link } from 'react-router-dom'
import { threadShow } from '../../api/threadsApi'
import { commentDelete } from '../../api/commentsApi'

class ThreadShow extends Component {
  constructor (props) {
    super(props)

    this.state = {
      thread: null,
      deleted: false
    }
  }

  componentDidMount () {
    const { match, msgAlert } = this.props
    threadShow(match.params.id)
      .then(res => this.setState({ thread: res.data.thread }))
      .then(() => {
        msgAlert({
          heading: 'Show thread: Success',
          message: 'Thread is being shown',
          variant: 'success'
        })
      })
      .catch(() => {
        msgAlert({
          heading: 'Failed to Show Thread',
          message: 'An error has occured while loading the thread',
          variant: 'danger'
        })
      })
  }

  handleDelete = id => {
    const { user, msgAlert } = this.props
    const { thread } = this.state

    if (user) {
      commentDelete(thread._id, id, user)
        .then(() => this.setState({ deleted: true }))
        .then(() => {
          msgAlert({
            heading: 'Comment Deletion: Success',
            message: 'Comment has been deleted',
            variant: 'success'
          })
        })
        .catch(() => {
          msgAlert({
            heading: 'Comment Deletion: Failed',
            message: 'You do not have permission to delete',
            variant: 'danger'
          })
        })
    }
  }

  render () {
    const { thread, deleted } = this.state
    const { user, match } = this.props

    if (!thread) {
      return (
        <Spinner animation="border" role="status">
          <span className="sr-only">Loading...</span>
        </Spinner>
      )
    }

    if (deleted) {
      return <Redirect to={`/category/${thread.category}/threads`} />
    }
    const formatDate = function (string) {
      const options = { year: 'numeric', month: 'long', day: 'numeric' }
      return new Date(string).toLocaleDateString([], options)
    }

    const threadDate = formatDate(thread.createdAt)
    if (user) {
      return (
        <div>
          <div className='post'>
            <h2>{thread.title}</h2>
            <h5>Created by: {thread.name}</h5>
            <h6>{thread.category}</h6>
            <p>{thread.post}</p>
            <p>{threadDate}</p>
            <Link to={`/category/${match.params.category}/threads`}>Go back</Link>
            <br></br>
            <Link to={`/category/${thread.category}/threads/${thread._id}/createComment`}>Create a Comment</Link>
          </div>
          <TableContainer component={Paper}>
            <Table className='showTable' stickyHeader aria-label="sticky table">
              <TableHead>
                <TableRow>
                  <TableCell align="right">Comment</TableCell>
                  <TableCell align="right">Username</TableCell>
                  <TableCell align="right">Created Date</TableCell>
                  <TableCell align="right">Update</TableCell>
                  <TableCell align="right">Delete</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {
                  thread.comments.map((comments, index) => {
                    return <TableRow key={index}>
                      <TableCell align="right">{comments.body}</TableCell>
                      <TableCell align="right">{comments.name}</TableCell>
                      <TableCell align="right">{formatDate(comments.createdAt)}</TableCell>
                      <TableCell align="right">
                        <Link to={`/category/${thread.category}/threads/${thread._id}/updateComment/${comments._id}`}>Update Comment</Link>
                      </TableCell>
                      <TableCell align="right">
                        <button id='deleteComment' onClick={() => this.handleDelete(comments._id)}>Delete Comment</button>
                      </TableCell>
                    </TableRow>
                  })
                }
              </TableBody>
            </Table>
          </TableContainer>
        </div>
      )
    }
    if (!user) {
      return (
        <div>
          <div className='post'>
            <h2>{thread.title}</h2>
            <h5>Created by: {thread.name}</h5>
            <h6>{thread.category}</h6>
            <p>{thread.post}</p>
            <p>{threadDate}</p>
            <Link to={`/category/${match.params.category}/threads`}>Go back</Link>
            <br></br>
            <Link to='/sign-in'>To create a comment, sign-in.</Link>
          </div>
          <TableContainer component={Paper}>
            <Table className='showTable' stickyHeader aria-label="sticky table">
              <TableHead>
                <TableRow>
                  <TableCell className='ShowHeader' align="right">Comment</TableCell>
                  <TableCell className='ShowHeader' align="right">Username</TableCell>
                  <TableCell className='ShowHeader' align="right">Created Date</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {
                  thread.comments.map((comments, index) => {
                    return <TableRow key={index}>
                      <TableCell align="right">{comments.body}</TableCell>
                      <TableCell align="right">{comments.name}</TableCell>
                      <TableCell align="right">{formatDate(comments.createdAt)}</TableCell>
                    </TableRow>
                  })
                }
              </TableBody>
            </Table>
          </TableContainer>
        </div>
      )
    }
  }
}

export default withRouter(ThreadShow)



