import React, { Component } from 'react'
import CommentForm from '../Forms/Commentform'
import { Redirect, Link, withRouter } from 'react-router-dom'
import { commentCreate } from '../../api/commentsApi'

class CommentCreate extends Component {
  constructor (props) {
    super(props)

    this.state = {
      comment: {
        body: '',
        name: '',
        threadId: ''
      },
      created: false
    }
  }
  handleSubmit = event => {
    event.preventDefault()
    const { user, msgAlert } = this.props
    const { comment } = this.state

    commentCreate(comment, user)
      .then(this.setState({ created: true }))
      .then(res => msgAlert({
        heading: 'Comment has been submitted',
        message: 'You are now viewing the thread.',
        variant: 'success'
      }))
      .catch(error => {
        msgAlert({
          heading: 'Comment was unable to be submitted',
          message: 'An error has occured. The error is: ' + error.message,
          variant: 'danger'
        })
      })
  }
  handleChange = event => {
    event.persist()
    this.setState(state => {
      return {
        comment: { ...state.comment, [event.target.name]: event.target.value }
      }
    })
  }

  render () {
    const { comment, created } = this.state
    const { match, user } = this.props
    if (created === true) {
      return <Redirect to={`/category/${match.params.category}/threads/${match.params.id}`} />
    }
    return (
      <div>
        <h3>Create a Comment</h3>
        <CommentForm
          comment={comment}
          match={match}
          user={user}
          handleChange={this.handleChange}
          handleSubmit={this.handleSubmit}
        />
        <Link to={`/category/${match.params.category}/threads/${match.params.id}`}>Go back</Link>
      </div>
    )
  }
}

export default withRouter(CommentCreate)



