import React, { Component } from 'react'
import { Link, Redirect, withRouter } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import Spinner from 'react-bootstrap/Spinner'
import { threadIndex, threadDelete } from '../../api/threadsApi'

class ThreadIndex extends Component {
  constructor (props) {
    super(props)

    this.state = {
      threads: null,
      deleted: false
    }
  }

  componentDidMount () {
    const { msgAlert } = this.props
    threadIndex()
      .then(res => this.setState({ threads: res.data.threads }))
      .catch(error => {
        msgAlert({
          heading: 'Failed to Show Threads',
          message: 'An error has occured while loading threads. The error is: ' + error.message,
          variant: 'danger'
        })
      })
  }

  handleDelete = id => {
    const { user, msgAlert } = this.props

    if (user) {
      threadDelete(id, user)
        .then(() => this.setState({ deleted: true }))
        .catch(error => {
          msgAlert({
            heading: 'Thread Deletion: Failed',
            message: 'You do not have permission to delete: ' + error.message,
            variant: 'danger'
          })
        })
    }
  }

  render () {
    const { threads, deleted } = this.state
    const { user } = this.props

    if (!threads) {
      return (
        <Spinner animation="border" role="status">
          <span className="sr-only">Loading...</span>
        </Spinner>
      )
    }
    if (deleted) {
      // redirect to the movies index page
      return <Redirect to='/' />
    }

    if (user) {
      return (
        <div>
          <Link to='/createThread'>Create a Thread</Link>
          <TableContainer component={Paper}>
            <Table className='indexTable' stickyHeader aria-label="sticky table">
              <TableHead>
                <TableRow>
                  <TableCell className='IndexHeader' align="right">Title</TableCell>
                  <TableCell className='IndexHeader' align="right">Post</TableCell>
                  <TableCell className='IndexHeader' align="right">Creator</TableCell>
                  <TableCell className='IndexHeader' align="right">Category</TableCell>
                  <TableCell className='IndexHeader' align="right"># of Comments</TableCell>
                  <TableCell className='IndexHeader' align="right">Delete</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {
                  threads.map((thread, index) => {
                    return <TableRow key={index}>
                      <TableCell align="right">
                        <Link className='indexLinks' to={`/category/${thread.category}/threads/${thread._id}`}>{thread.title}</Link>
                      </TableCell>
                      <TableCell align="right">{thread.post}</TableCell>
                      <TableCell align="right">{thread.name}</TableCell>
                      <TableCell align="right">{thread.category}</TableCell>
                      <TableCell align="right">{thread.comments.length}</TableCell>
                      <TableCell align="right">
                        <button id='deleteThread' onClick={() => this.handleDelete(thread._id)}>Delete Thread</button>
                      </TableCell>
                    </TableRow>
                  })
                }
              </TableBody>
            </Table>
          </TableContainer>
        </div>
      )
    }
    return (
      <TableContainer component={Paper}>
        <Table className='indexTable' stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              <TableCell align="right">Title</TableCell>
              <TableCell align="right">Post</TableCell>
              <TableCell align="right">Creator</TableCell>
              <TableCell align="right">Category</TableCell>
              <TableCell align="right"># of Comments</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {
              threads.map((thread, index) => {
                return <TableRow key={index}>
                  <TableCell align="right">
                    <Link to={`/category/${thread.category}/threads/${thread._id}`}>{thread.title}</Link>
                  </TableCell>
                  <TableCell align="right">{thread.post}</TableCell>
                  <TableCell align="right">{thread.name}</TableCell>
                  <TableCell align="right">{thread.category}</TableCell>
                  <TableCell align="right">{thread.comments.length}</TableCell>
                </TableRow>
              })
            }
          </TableBody>
        </Table>
      </TableContainer>
    )
  }
}

export default withRouter(ThreadIndex)



