import React from 'react'

const CommentForm = ({ comment, match, user, handleSubmit, handleChange }) => (
  <form onSubmit={handleSubmit}>
    <label>Body</label>
    <input
      required
      maxLength='200'
      placeholder='Enter comment'
      name='body'
      value={comment.body}
      onChange={handleChange}
    />
    <br></br>
    <label>Username</label>
    <select required placeholder='Choose your username' name='name' value={comment.name} onChange={handleChange}>
      <option disabled={true} value="" className='placeholderValue'>Choose your username</option>
      <option value={user.username}>{user.username}</option>
    </select>
    <br></br>
    <label>ThreadId</label>
    <select required placeholder='Choose the thread ID' name='threadId' value={comment.threadId} onChange={handleChange}>
      <option disabled={true} value="" className='placeholderValue'>Choose the thread ID</option>
      <option value={match.params.id}>Thread Id</option>
    </select>
    <button type='submit'>Submit</button>
  </form>
)

export default CommentForm



