import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import Spinner from 'react-bootstrap/Spinner'
import { threadIndex } from '../../api/threadsApi'

class Homepage extends Component {
  constructor (props) {
    super(props)

    this.state = {
      threads: null,
      deleted: false
    }
  }

  componentDidMount () {
    const { msgAlert } = this.props
    threadIndex()
      .then(res => this.setState({ threads: res.data.threads }))
      .catch(error => {
        msgAlert({
          heading: 'Failed to Show Threads',
          message: 'An error has occured while loading threads. The error is: ' + error.message,
          variant: 'danger'
        })
      })
  }

  render () {
    const { threads } = this.state

    if (!threads) {
      return (
        <Spinner animation="border" role="status">
          <span className="sr-only">Loading...</span>
        </Spinner>
      )
    }

    function createData (category, description) {
      return { category, description }
    }

    const rows = [
      createData('Premier League', 'Everything about England\'s top flight'),
      createData('Serie A', 'Serie A News and Discussions'),
      createData('La Liga', 'All about La Liga'),
      createData('Ligue 1', 'Discussions and Information about France\'s top league'),
      createData('Bundesliga', 'Germany\'s top league'),
      createData('Other Leagues', 'Discuss about lesser-known leagues here'),
      createData('International', 'News about International teams'),
      createData('Bootroom', 'Discuss soccer in general'),
      createData('Off Topic', 'Discuss anything in general')
    ]

    const calculateNumOfComments = function (threads) {
      let numOfComments = 0
      threads.map((thread) => {
        numOfComments += thread.comments.length
      })
      return numOfComments
    }

    const totalNumOfComments = calculateNumOfComments(threads)

    return (
      <TableContainer component={Paper}>
        <Table className='indexTable' aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell className='HomepageHeader'>Category</TableCell>
              <TableCell className='HomepageHeader' align="right">Description</TableCell>
              <TableCell className='HomepageHeader' align="right">Threads</TableCell>
              <TableCell className='HomepageHeader' align="right">Comments</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              <TableRow key={row.category}>
                <TableCell className="categories" component="th" scope="row">
                  <Link to={`/category/${row.category}/threads`}>{row.category}</Link>
                </TableCell>
                <TableCell align="right">{row.description}</TableCell>
                <TableCell align="right">{threads.length}</TableCell>
                <TableCell align="right">{totalNumOfComments}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    )
  }
}

export default withRouter(Homepage)

